html,body{height:100%;min-height:100%;width:100vw;margin:0;padding:0;line-height:75%;box-sizing:border-box}h3{font-family:"Metrophobic","Trebuchet MS",sans-serif;font-size:50px;font-weight:400;line-height:100%;color:#c84c1f;margin:10px 0px}h4{font-family:"Libre Caslon Display","Georgia",serif,serif;font-size:30px;font-weight:400;color:#1E1F1E}h5{font-family:"Libre Caslon Display","Georgia",serif;font-size:20px;font-weight:700;color:#1E1F1E}.content{font-family:"Libre Caslon Display","Georgia",serif;font-size:20px;font-weight:200;color:#1E1F1E;line-height:220%;background-color:#f8f4e8;height:auto;min-height:auto;width:100vw;padding:30px 90px 20px 90px;position:relative;display:block;box-sizing:border-box;margin:0}a{color:#c84c1f;text-decoration:none}a :hover{color:#1E1F1E}.error-text{font-family:'Courier';color:black;cursor:not-allowed}#invisible-text{color:#f8f4e8}.content-image{width:auto;height:auto}iframe{width:100%;height:600px}

.ribbon{font-family:"Libre Caslon Display","Georgia",serif;color:#1E1F1E;background-color:#E9D286;width:100vw;height:275px;padding:35px;display:block;position:relative;margin:auto;box-sizing:border-box;text-align:center}.ribbon svg{height:100px;color:#c84c1f;display:inline-block}.ribbon h1{color:#1E1F1E;font-family:"Metrophobic","Trebuchet MS",sans-serif;font-size:85px;font-weight:900;text-align:center}

nav{font-family:"Libre Caslon Display","Georgia",serif;font-size:22px;font-weight:200;text-decoration:none;color:#c84c1f;background-color:#E9D286;height:49px;position:-webkit-sticky;position:sticky;top:-1px;z-index:1;border-bottom:10px solid #c84c1f;text-align:center}nav ul{list-style-type:none;margin:0;padding:0}nav li{display:inline}nav li a{color:#c84c1f;padding:19px;transition:all 0.5s linear;display:inline-block}nav li a:hover{color:#1E1F1E}nav .active{color:#1E1F1E;background-color:#c84c1f}

footer{font-family:"Libre Caslon Display","Georgia",serif;font-size:12px;color:#1E1F1E;background-color:#E9D286;width:100vw;height:225px;border-top:5px solid #c84c1f;display:block;bottom:0px}footer .footer-text{font-family:"Libre Caslon Display","Georgia",serif;font-size:12px;text-align:center;line-height:150%;margin-top:70px}

.homepage-content{width:100vw;margin:0px -80px;padding:0px;font-size:26px}.homepage-content .content-block{box-sizing:border-box;height:auto;display:block;position:relative;margin-left:-10px}.homepage-content .block-textwrap{height:400px;width:600px;padding:50px;box-sizing:border-box;position:absolute;top:50%;transform:translateY(-50%);margin:auto}.homepage-content .homepage-bg-image{width:100vw;min-width:600px;height:100%;margin:0;z-index:-1;box-sizing:default}.homepage-content .overlay{position:absolute;top:0;left:0;right:0;bottom:0;width:100%;height:99%;z-index:0}.homepage-content .welcome{padding:20px 50px}.homepage-content .welcome h3{text-align:center}.homepage-content #banner-image{width:100vw;height:100%;display:block;position:relative;margin:0;margin-left:-10px}.homepage-content .learn .overlay{background:linear-gradient(to right, rgba(56,51,51,0.1), 40%, rgba(56,51,51,0.9))}.homepage-content .learn .block-textwrap{color:#f8f4e8;width:700px;height:100%;position:absolute;right:3%;top:60%;transform:translateY(-50%);vertical-align:middle}.homepage-content .learn .block-textwrap h3{color:#f8f4e8}.homepage-content .custom{margin-bottom:-20px;padding:70px}.homepage-content .robots{margin-bottom:-10px}.homepage-content .robots .overlay{background:linear-gradient(to right, rgba(56,51,51,0.9), 60%, rgba(56,51,51,0.1))}.homepage-content .robots .block-textwrap{color:#f8f4e8;height:100%;position:absolute;left:6%;top:60%;transform:translateY(-50%);vertical-align:middle}.homepage-content .robots .block-textwrap h3{color:#f8f4e8}.homepage-content .pricing{padding-bottom:-20px;padding:70px;background-color:#c84c1f;color:#f8f4e8}.homepage-content .pricing h3{color:#f8f4e8}.homepage-content .build{padding:75px;text-align:center;font-size:50px}.homepage-content .build a:hover{color:#f8bbb6}

.about-content img{width:auto;height:auto;float:right;margin:0px 30px 30px 30px}

.playground-content .playground-intro{background-color:#c84c1f;margin:-30px -90px;font-size:26px;padding:40px;text-align:center}.playground-content .playground-intro h3,.playground-content .playground-intro h4{color:#f8f4e8}.playground-content .instructions{font-family:"Metrophobic","Trebuchet MS",sans-serif;font-size:20px;text-align:center;margin:50px}.playground-content hr{border-top:2px solid #E9D286}.playground-content .gender-icon-wrapper{display:block;margin:auto;text-align:center;padding:40px}.playground-content .gender-icon-wrapper svg{color:#f8bbb6;transition:0.2s;margin:30px;height:300px;display:inline-block}.playground-content .gender-icon-wrapper svg:hover{transform:scale(1.2, 1.2);shadow:5px 5px 30px 15px #1E1F1E,-5px -5px 30px 15px #1E1F1E}.playground-content .gender-icon-wrapper svg.selected{color:#c84c1f;transform:scale(1.3, 1.3)}.playground-content .gender-icon-wrapper #venus path{margin-top:30px}.playground-content .face-select-wrapper{text-align:center}.playground-content .face-select-wrapper .face-select{margin:auto;max-width:60vw}.playground-content .trait-select .trait-cloud{width:300px}.playground-content .trait-select .trait-cloud .selected{color:#c84c1f}

